@font-face {
  font-family: "Montserrat-Regular";
  font-weight: 400;
  src: url("../../assets/fonts/Montserrat-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Montserrat-Medium";
  font-weight: 500;
  src: url("../../assets/fonts/Montserrat-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Montserrat-SemiBold";
  font-weight: 600;
  src: url("../../assets/fonts/Montserrat-SemiBold.ttf") format("truetype");
}
@font-face {
  font-family: "Montserrat-Bold";
  font-weight: 700;
  src: url("../../assets/fonts/Montserrat-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Montserrat-ExtraBold";
  font-weight: 800;
  src: url("../../assets/fonts/Montserrat-ExtraBold.ttf") format("truetype");
}
@font-face {
  font-family: "Lato-Bold";
  font-weight: 700;
  src: url("../../assets/fonts/Lato-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Lato-Regular";
  font-weight: 400;
  src: url("../../assets/fonts/Lato-Regular.ttf") format("truetype");
}
* {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Montserrat-Regular", sans-serif;
  font-size: 16px;
}

img {
  display: block;
  box-sizing: content-box;
}

a {
  text-decoration: none;
}

button {
  background-color: unset;
  border: none;
}

main {
  max-width: 1200px;
  width: 100%;
  padding: 10px;
  margin: 0 auto;
}

@media (max-width: 1920px) {
  main {
    padding: 15px;
  }
}
@media (max-width: 1220px) {
  main {
    padding: 15px;
    width: 100%;
  }
}
@media (max-width: 767px) {
  body {
    font-size: 14px;
  }

  main {
    max-width: 737px;
    padding: 15px;
    width: 100%;
  }
}
@import-normalize;
.header {
  max-width: 1920px;
  width: 100%;
  display: flex;
  flex-direction: row;
  height: 152px;
  align-items: center;
  margin: 0 auto;
}

.header--logo {
  width: 100px;
}

.header--logo-mgLeft {
  margin-left: 63px;
}

.header--burger {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  position: relative;
}

.header--burger-mgLeft {
  margin-left: 51px;
}

.header--burger img:hover {
  width: 60px;
  cursor: pointer;
}

.header--burgerMenu-nonactive {
  display: none;
}

.header--burgerMenu-active {
  display: block;
}

.header--burgerMenu {
  position: absolute;
  top: 138px;
  left: 0px;
  width: 500px;
  background-color: #4c858b;
  padding: 50px;
}
.header--burgerMenu ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 36px;
}
.header--burgerMenu ul .header--navItem a {
  color: #0c2f33;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 24px;
  font-family: "Montserrat-Bold", sans-serif;
  transition: ease-out 0.5s;
}
.header--burgerMenu ul .header--navItem a:hover {
  color: #ffffff;
}

.header--nav ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 36px;
}
.header--nav ul .header--navItem a {
  color: #215359;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  font-family: "Montserrat-Bold", sans-serif;
  transition: ease-out 0.5s;
}
.header--nav ul .header--navItem a:hover {
  color: #e33e54;
}

.header--nav-mgLeft {
  margin-left: 30px;
}

.header--phoneLink {
  letter-spacing: 0.08em;
  color: #215359;
}
.header--phoneLink span {
  color: #000000;
  font-family: "Montserrat-Bold", sans-serif;
}

.header--phoneLink-mgLeft {
  margin-left: 98px;
}

.header--callMeLink {
  border-bottom: 1px dotted #ae2236;
  padding-bottom: 5px;
}
.header--callMeLink a {
  font-size: 14px;
  color: #ae2236;
  text-transform: uppercase;
  font-family: "Montserrat-Bold", sans-serif;
}

.header--callMeLink-mgLeft {
  margin-left: 71px;
}

.header--socialContainer {
  display: flex;
  flex-direction: row;
  gap: 15px;
}

.header--socialContainer-mgLeft {
  margin-left: 63px;
}

.header-socialItem svg {
  width: 20px;
}

.header-socialItem:hover svg {
  fill: #e33e54;
}

.header--toolsContainer {
  display: flex;
  flex-direction: row;
  gap: 16px;
}
.header--toolsContainer .header--toolsItem {
  height: 56px;
  width: 56px;
  border-radius: 50%;
  border: 1px solid #e5e5e5;
  position: relative;
  transition: ease-out 0.2s;
}
.header--toolsContainer .header--toolsItem img {
  position: absolute;
  top: 15px;
  left: 17px;
}
.header--toolsContainer .header--toolsItem:hover {
  cursor: pointer;
  border: 1px solid #215359;
}

.header--cartTag {
  position: absolute;
  left: 40px;
  top: 3px;
  border-radius: 50%;
  width: 21px;
  height: 21px;
  background-color: #e33e54;
  color: #ffffff;
  font-family: "Lato-Bold", sans-serif;
  font-weight: 800;
  font-size: 11px;
  line-height: 16px;
}

.header--toolsContainer-mgLeft {
  margin-left: 60px;
}

@media (max-width: 1919px) {
  #header-navMenu {
    display: none;
  }

  .header {
    justify-content: space-evenly;
  }
}
@media (max-width: 1220px) {
  .header {
    height: 112px;
    max-width: 1220px;
  }

  .header--logo {
    width: 64px;
  }

  .header--logo-mgLeft {
    margin-left: 16px;
  }

  .header--phoneLink {
    display: none;
  }

  .header--callMeLink {
    display: none;
  }

  .header--socialContainer {
    display: none;
  }

  .header--burger-fixLeft {
    order: 3;
    margin-left: 23px;
    margin-right: 16px;
  }

  .header--toolsContainer {
    order: 2;
    margin-left: auto;
    gap: 25px;
  }
}
@media (max-width: 767px) {
  .header {
    height: 79px;
    max-width: 767px;
  }

  .header--logo {
    width: 48px;
  }

  .header--search {
    display: none;
  }

  .header--heart {
    display: none;
  }

  .header--toolsContainer {
    display: flex;
    flex-direction: row;
    gap: 16px;
  }
  .header--toolsContainer .header--toolsItem {
    height: 44px;
    width: 44px;
  }
  .header--toolsContainer .header--toolsItem img {
    top: 10px;
    left: 10px;
    width: 20px;
  }

  .header--cartTag {
    left: 25px;
    top: 1px;
  }

  .header--burger img {
    width: 44px;
    height: 44px;
  }

  .header--burger-fixLeft {
    margin-left: 18px;
    margin-right: 2px;
  }

  .header--burgerMenu {
    padding: 50px 10px;
    max-width: 300px;
    width: 100%;
  }
}
.footer--wrapper {
  width: 100%;
  background-color: #0c2f33;
  height: 350px;
  padding: 10px;
}

.footer {
  width: 1220px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 438px 472px 312px;
}

.footer--item {
  margin-top: 88px;
}
.footer--item .footer--itemHeader {
  color: #ffffff;
  font-family: "Montserrat-Bold", sans-serif;
  font-size: 22px;
  text-transform: uppercase;
  margin-bottom: 17px;
}
.footer--item .footer--catalogue .footer--catalogueList {
  list-style-type: none;
  display: grid;
  grid-template-columns: 207px auto;
  row-gap: 18px;
}
.footer--item .footer--catalogue .footer--catalogueList .footer--catalogueListItem a {
  color: #ffffff;
  font-size: 14px;
  line-height: 17.07px;
}
.footer--item .footer--catalogue .footer--catalogueList .footer--catalogueListItem a:hover {
  text-decoration: underline;
}

.footer--timeItem .footer--time {
  margin: 15px 0;
}
.footer--timeItem .footer--time .footer-timeParagragh {
  font-family: "Lato-Regular", sans-serif;
  font-size: 14px;
  font-style: normal;
  color: #ffffff;
}
.footer--timeItem .footer--time .footer-timeParagragh .footer--days {
  color: #4c858b;
}
.footer--timeItem .footer--social {
  display: flex;
  flex-direction: row;
  gap: 42px;
  margin-top: 30px;
}
.footer--timeItem .footer--social .footer--socialItem svg {
  width: 21px;
  fill: #4c858b;
}
.footer--timeItem .footer--social .footer--socialItem:hover svg {
  fill: #e33e54;
}

.footer--social-nonactive {
  display: none;
}

@media (max-width: 1220px) {
  .footer--wrapper {
    height: 70px;
  }

  .footer {
    display: none;
  }
  .footer .footer--social {
    display: block;
    text-align: center;
  }

  .footer--social-nonactive {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 42px;
    margin-top: 18px;
  }
  .footer--social-nonactive .footer--social-nonactive svg {
    width: 34px;
  }
}
.hero--header {
  font-family: "Montserrat-Bold", sans-serif;
