.banner-image .banner--blob-image {
  position: absolute;
  top: -8px;
  left: -15px;
  z-index: 1;
}
.banner-image .banner--chimsey-image {
  position: absolute;
  top: -35px;
  left: 164px;
  z-index: 2;
}
.banner-image .banner--dotsLeft-image {
  position: absolute;
  top: -3px;
  left: 130px;
  z-index: 2;
}
.banner-image .banner--dotsRight-image {
  position: absolute;
  right: -99px;
  bottom: -28px;
  z-index: 2;
}
.banner-image .banner--triangleLeft-image {
  position: absolute;
  top: 140px;
  left: 2px;
  z-index: 2;
}
.banner-image .banner--triangleRight-image {
  position: absolute;
  top: 47px;
  right: -158px;
